import React from "react";
import "./Detail.css"

class Detail extends React.Component{
  componentDidMount(){
    const { location, history } = this.props;
    if (location.state === undefined){
      history.push("/");
      //만약 location.state가 undefined이면, 페이지를 "/"로 이동시킨다. 
    }
  }
  // render 후 componentDidMount로 넘어간다. 그래서 location이 없으면, Error발생
  render() {
    const { location } = this.props;
    // 해결책 1
    if (location.state) {
      return (
        <div className="detail_container">
          <div className="detail_poster">
            <img src={location.state.poster}></img>
          </div>
          <div className="detail_info">
            <span>{location.state.title}</span>
            <p>{location.state.year}</p>
            <ul className="detail_genres">
              {location.state.genres.map((genre, index) =>(
                <li key={index} className="detail_genre">{genre}</li>
              ))}
            </ul>
            <p className="detail_summary">{location.state.summary}</p>
          </div>
        </div>
      );
    } else {
      return null;
    }
  }
}

export default Detail;